<template>
  <div class="convenience">
    <h3 class="convenience__title">   </h3>
    <div class="convenience__items">
      <!-- Display only the first 5 items on mobile -->
      <div
        v-for="(item, index) in displayedItems"
        :key="index"
        class="convenience__item"
      >
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M16.7702 13.91C16.7702 16.5444 14.6346 18.68 12.0002 18.68C9.36576 18.68 7.23016 16.5444 7.23016 13.91M16.7702 13.91C16.7702 11.2756 14.6346 9.14 12.0002 9.14C9.36576 9.14 7.23016 11.2756 7.23016 13.91M16.7702 13.91C12.9502 15.82 12.0002 11.05 7.23016 13.91M6.27016 5.32H13.0502M15.8202 5.32H17.7302M6.27016 1.5H17.7302C18.4887 1.5 19.2161 1.80132 19.7525 2.33767C20.2888 2.87403 20.5902 3.60148 20.5902 4.36V22.5H3.41016V4.36C3.41016 3.60148 3.71148 2.87403 4.24783 2.33767C4.78418 1.80132 5.51164 1.5 6.27016 1.5Z"
            stroke="#424242"
            stroke-width="1.5"
            stroke-miterlimit="10"
            stroke-linecap="round"
          />
        </svg>
        {{ item }}
      </div>
      <!-- Button to show all items in a popup -->
      <Button
        type="secondary"
        size="big"
        v-if="isMobile && remainingItemsCount > 0"
        @click="showAllItems"
      >
          : {{ remainingItemsCount }}
      </Button>
    </div>

    <!-- Popup modal to show all convenience items -->
    <div v-if="showPopup" class="convenience__popup">
      <Button class="hotel-detail__button" type="text" @click="closePopup">
        <svg
          width="20"
          height="20"
          viewBox="0 0 20 20"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M12.5 5L7.5 10L12.5 15"
            stroke="#2196F3"
            stroke-width="1.5"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
        
      </Button>
      <h3 class="convenience__title">   </h3>
      <div class="convenience__popup-content">
        <div
          v-for="(item, index) in photos"
          :key="index"
          class="convenience__item"
        >
          <svg
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M16.7702 13.91C16.7702 16.5444 14.6346 18.68 12.0002 18.68C9.36576 18.68 7.23016 16.5444 7.23016 13.91M16.7702 13.91C16.7702 11.2756 14.6346 9.14 12.0002 9.14C9.36576 9.14 7.23016 11.2756 7.23016 13.91M16.7702 13.91C12.9502 15.82 12.0002 11.05 7.23016 13.91M6.27016 5.32H13.0502M15.8202 5.32H17.7302M6.27016 1.5H17.7302C18.4887 1.5 19.2161 1.80132 19.7525 2.33767C20.2888 2.87403 20.5902 3.60148 20.5902 4.36V22.5H3.41016V4.36C3.41016 3.60148 3.71148 2.87403 4.24783 2.33767C4.78418 1.80132 5.51164 1.5 6.27016 1.5Z"
              stroke="#424242"
              stroke-width="1.5"
              stroke-miterlimit="10"
              stroke-linecap="round"
            />
          </svg>
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "../button/Button.vue";

export default {
  components: { Button },
  data() {
    return {
      photos: [
        " ",
        "",
        "",
        " ",
        "",
        "",
        " ",
        "",
        " ",
        "",
        "",
        " ",
        "",
        "",
        " ",
        "",
      ],
      showPopup: false,
      isMobile: window.matchMedia("(max-width: 1023px)").matches, // Example breakpoint
    };
  },
  computed: {
    displayedItems() {
      return this.isMobile ? this.photos.slice(0, 5) : this.photos;
    },
    remainingItemsCount() {
      return this.isMobile ? this.photos.length - 5 : 0;
    },
  },
  methods: {
    showAllItems(event) {
      event.preventDefault();
      this.showPopup = true;
    },
    closePopup(event) {
      event.preventDefault();
      this.showPopup = false;
    },
    handleMediaChange(event) {
      this.isMobile = event.matches;
    },
  },
  mounted() {
    const mediaQuery = window.matchMedia("(max-width: 1023px)");

    this.handleMediaChange(mediaQuery);

    // ,   addEventListener
    if (mediaQuery.addEventListener) {
      mediaQuery.addEventListener("change", this.handleMediaChange);
    } else if (mediaQuery.addListener) {
      mediaQuery.addListener(this.handleMediaChange); //  addListener  iOS
    }
  },
  beforeUnmount() {
    const mediaQuery = window.matchMedia("(max-width: 1023px)");

    // ,   addEventListener
    if (mediaQuery.addEventListener) {
      mediaQuery.removeEventListener("change", this.handleMediaChange);
    } else if (mediaQuery.addListener) {
      mediaQuery.removeListener(this.handleMediaChange); //  addListener  iOS
    }
  },
};
</script>

<style lang="scss">
@import "/src/styles/common.scss";
@import "./convenience.scss";
</style>
